interface SlackAttachmentAction {
    name: string
    text: string
    value: string
    type: "button"
    style: "default" | "primary" | "danger"
}

interface SlackAttachment {
    fallback: string
    color: string
    author_name?: string
    title?: string
    text?: string
    actions?: SlackAttachmentAction[]
}

interface SlackPayload {
    text?: string;
    username?: string;
    icon_url?: string
    icon_emoji?: SlackEmoji
    attachments?: SlackAttachment[]
}

class SlackBotClass {

    hook: string

    constructor(hookUrl: string) {
        this.hook = hookUrl
    }

    async sendPayloadToSlack(payload: SlackPayload): Promise<boolean> {
        try {
            await fetch(this.hook, {
                method: 'POST',
                body: JSON.stringify(payload)
            })
            return true
        }
        catch (error) {
            console.warn(error)
            return false
        }
    }

    link = (url: string, label: string) => ` <${url}|${label}>`

    mapsUrl = (postal: string, city: string, country: string) => `https://www.google.com/maps/?q=${postal} ${city} ${country}`


    sendTextMessage(text: string) {
        this.sendPayloadToSlack({
            text
        })
    }


    sendSiteVisitNotification = (ipdata: IPData) => {
        this.sendPayloadToSlack({
            icon_emoji: ':zap:',
            attachments: [
                {
                    color: '#',
                    title: 'Site Notification',
                    fallback: '',
                    text: ''
                }
            ]
        })
    }

}

